const csrfToken = getMeta("csrf-token");
let jwt = getCookie("jwt");
export function refreshToken() {
    jwt = getCookie("jwt");
}
export async function apiActionRequest(action, viewName = "", keys, values) {
    const data = keys.reduce((obj, key, index) => {
        // Converts key values to object
        obj[key] = values[index];
        return obj;
    }, {});
    const request = {
        action: action,
        jwt: jwt,
        view_name: viewName,
        row: data,
    };
    const res = await fetch(window.location.origin + "/app/execute/", {
        method: "POST",
        headers: {
            "Content-Type": "application/json",
            "X-CSRFToken": csrfToken,
        },
        body: JSON.stringify(request),
    });
    const rows = [];
    const resJson = await res.json();
    if (resJson.status !== 200) {
        // If api returns error
        console.log("Error: " + resJson.message);
        return undefined;
    }
    else {
        return { names: resJson.row_keys, table: resJson.rows };
    }
}
export async function apiTreeRequest() {
    let res = await fetch(window.location.origin + "/app/viewtree/", {
        method: "GET",
        headers: {
            "Content-Type": "application/json",
            "X-CSRFToken": csrfToken,
        },
    });
    return await res.json();
}
export function getMeta(metaName) {
    const metas = document.getElementsByTagName("meta");
    for (let i = 0; i < metas.length; i++) {
        if (metas[i].getAttribute("name") === metaName) {
            return metas[i].getAttribute("content");
        }
    }
    return "";
}
export function getCookie(name) {
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2)
        return parts.pop().split(";").shift();
}
export function setCookie(name, value, hours) {
    let expires = "";
    if (hours) {
        const date = new Date();
        date.setTime(date.getTime() + hours * 60 * 60 * 1000);
        expires = `; expires=${date.toUTCString()}`;
    }
    document.cookie = `${name}=${value || ""}${expires}; path=/; SameSite=Lax;`;
}
